import React, { useState } from 'react';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import TopPannel from '../TopPanel';
import { toast } from 'react-toastify';
import AdminServices from 'services/api/AdminServices';
import { baseImgURL } from 'assets/Constant';

export default function MfTransactionPin(props) {
  const [clientData, setClientData] = useState({});
  const [loading, setLoading] = useState();

  React.useEffect(()=>{
    setLoading(true)
    try {
      db.get(db.model.user).then(async (user) => {
        await getClientData(user.userData.id)
        setLoading(false)
      })
    } catch (error) {
      toast.error(error);
      setLoading(false)
    }
  },[])

  const getClientData = async(id)=>{
    await db.get(db.model.clientData).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getClient(id).then(res => res);
        if (res.type === true) {
          const a = JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get Client API :", aa)
          setClientData(aa)
          await db.set(db.model.clientData, JSON.stringify(aa))
        }
      } else {
        setClientData(p)
      }
    })
  }

  return (
    <div className='mainContainer'>
      <MainHeader header='MF transaction PIN' navigation={props.history} />
      <div className='dataContainer px-0'>
        <TopPannel/>
        {
            clientData?.Client?.[0].PIN === ''?
                <div className='menu-card' onClick={()=>props.history.push({
                  pathname: '/menu/mf-transaction-pin/new-pin',
                  state : {type:'new',clientId:clientData?.Client?.[0].ClientId}
                })}>
                    <img src={baseImgURL+"menu_pin.webp"} className='menu-card-icon' />
                    <div className='menu-card-body'>
                    <div className='bodyTextDark'>Add MF transaction PIN</div>
                    <div className='smallTextLight'>We strongly recommend you to add an MF transaction PIN to protect your investments</div>
                    </div>
                </div>
                :
                <>
                    <div className='menu-card' onClick={()=>props.history.push({
                      pathname: '/menu/mf-transaction-pin/current-pin',
                      state : {type:'change',clientId:clientData?.Client?.[0].ClientId,currentPin:clientData?.Client?.[0].PIN}
                    })}>
                        <img src={baseImgURL+"menu_pin.webp"} className='menu-card-icon' />
                        <div className='menu-card-body'>
                            <div className='bodyTextDark'>Change MF transaction PIN</div>
                        </div>
                    </div>
                    {/* <div className='menu-card' onClick={()=>props.history.push()}>
                        <img src={baseImgURL+"menu_pin.webp"} className='menu-card-icon' />
                        <div className='menu-card-body'>
                            <div className='bodyTextDark' style={{color:cssVar.getPropertyValue('--red')}}>Remove MF transaction PIN</div>
                            <div className='smallTextLight'>We strongly recommend you to keep the pin to protect your investment</div>
                        </div>
                    </div> */}
                </>
        }
      </div>
    </div>
  )
}
